.portfolio {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
  background-color: #0a192f;
  color: #8892b0;
}
.portfolio h2 {
  text-align: center;
  margin-bottom: 20px;
  font-family: "Moderustic", sans-serif;
  color: #ccd6f6;
}
.portfolio p {
  text-align: center;
  margin-bottom: 40px;
  font-family: "Outfit", system-ui;
}
.portfolio p a {
  color: #64ffda;
  text-decoration: none;
}
.portfolio p a:hover {
  text-decoration: underline;
}
.portfolio .project-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 25px;
}
@media (max-width: 1024px) {
  .portfolio .project-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 768px) {
  .portfolio .project-grid {
    grid-template-columns: 1fr;
  }
}
.portfolio .project-card {
  display: block;
  text-decoration: none;
  color: inherit;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  background-color: #112240;
}
.portfolio .project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 12px rgba(100, 255, 218, 0.1);
}
.portfolio .project-card:hover .project-description {
  opacity: 1;
  transform: translateY(0);
}
.portfolio .project-image {
  height: 200px;
  background-size: cover;
  background-position: center;
  position: relative;
}
.portfolio .project-image .project-description {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(17, 34, 64, 0.9);
  color: #ccd6f6;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.3s, transform 0.3s;
}
.portfolio .project-image .project-description h3 {
  font-size: 1.2rem;
  margin-bottom: 8px;
  color: #64ffda;
}
.portfolio .project-image .project-description p {
  font-size: 0.9rem;
  text-align: center;
  margin-bottom: 10px;
}
.portfolio .project-image .project-description .tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-top: 8px;
  justify-content: center;
}
.portfolio .project-image .project-description .tech-stack .tech-badge {
  background-color: #233554;
  color: #64ffda;
  padding: 4px 8px;
  font-size: 0.8rem;
  border-radius: 4px;
}
.portfolio .project-image .project-description .view-project-button {
  display: inline-block;
  background-color: transparent;
  color: #64ffda;
  text-decoration: none;
  padding: 6px 12px;
  border: 1px solid #64ffda;
  border-radius: 4px;
  margin-top: 10px;
  font-size: 0.9rem;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.portfolio .project-image .project-description .view-project-button:hover {
  background-color: rgba(100, 255, 218, 0.1);
}/*# sourceMappingURL=portfolio.css.map */